import React from "react";
import body from "../../../assets/images/check/body.png";
import computer from "../../../assets/images/check/computer.png";
import kpp from "../../../assets/images/check/kpp.png";
import engine from "../../../assets/images/check/engine.png";
import lawyer from "../../../assets/images/check/lawyer.png";
import running from "../../../assets/images/check/running.png";
import { CheckCard } from "../../cards/checkCard";
import styles from "./Check.module.scss";
import cn from "classnames";
import carInMeddle from "../../../assets/images/check/carInMeddle.png";
import carInMiddleMobile from "../../../assets/images/check/cardInMiddleMobile.png";
import { AnimateWrapper } from "../../animateWrapper";

const CheckDesktop = () => {
  return (
    <div className={styles.desktop}>
      {/*<img src={carInMeddle} alt={"carInMeddle"} className={styles.car} />*/}
      <div className={cn(styles.row, styles.first)}>
        <AnimateWrapper animate={"fade-left"}>
          <CheckCard
            src={body}
            title={""}
            text={
              "   , ,  , , - ."
            }
            num='1'
          ></CheckCard>
        </AnimateWrapper>
        <AnimateWrapper animate={"fade-right"}>
          <CheckCard
            src={engine}
            title={""}
            text={
              " ,   ,    ."
            }
            isReversed={true}
            num='2'
          />
        </AnimateWrapper>
      </div>
      <div className={cn(styles.row, styles.second)}>
        <AnimateWrapper animate={"fade-left"}>
          <CheckCard
            src={kpp}
            title={""}
            text={
              "   ,  -,    , ."
            }
            num='3'
          />
        </AnimateWrapper>

        <AnimateWrapper animate={"fade-right"}>
          <CheckCard
            src={running}
            title={" "}
            text={
              "     ,   , ."
            }
            isReversed={true}
            num='4'
          />
        </AnimateWrapper>
      </div>
      <div className={cn(styles.row, styles.third)}>
        <AnimateWrapper animate={"fade-left"}>
          <CheckCard
            src={computer}
            title={" "}
            text={
              "   .     ."
            }
            num='5'
          />
        </AnimateWrapper>

        <AnimateWrapper animate={"fade-right"}>
          <CheckCard
            src={lawyer}
            title={" "}
            text={
              "     , , ,    ."
            }
            isReversed={true}
            num='6'
          />
        </AnimateWrapper>
      </div>
    </div>
  );
};

const CheckMobile = () => {
  return (
    <div className={styles.mobile}>
      <div className={styles.column}>
        <CheckCard
          src={body}
          title={""}
          text={
            "   , ,  , , - ."
          }
          isReversed={false}
            num='1'
        />
        <CheckCard

          src={engine}
          title={""}
          text={
            " ,   ,    ."
          }
          isReversed={true}
          num='2'
        />
        <CheckCard
            num='3'
          src={kpp}
          title={""}
          text={
            "   ,  -,    , ."
          }
          isReversed={false}
        />
      </div>
      <div>
        <img
          src={carInMiddleMobile}
          alt={"carInMeddle"}
          className={styles.carMobile}
        />
      </div>
      <div className={styles.column}>
        <CheckCard
          src={running}
          title={" "}
          text={
            "     ,   , ."
          }
          isReversed={true}
          num='4'
        />
        <CheckCard
            num={'Hello'}
            src={computer}
            title={" "}
            text={
            "   .     ."
            }
            isReversed={false}
            num='5'
        />
        <CheckCard
          src={lawyer}
          title={" "}
          text={
            "     , , ,    ."
          }
          isReversed={true}
          num='6'
        />
      </div>
    </div>
  );
};

export const Check = () => {
  return (
    <div className={styles.wrapper}>
      <h2 className={styles.title}>   </h2>
      <div className={styles.content}>
        <CheckDesktop />
        <CheckMobile />
      </div>
    </div>
  );
};
