import React from 'react';
import styles from './Garantier.module.scss'
import cn from "classnames";
import letter from '../../../assets/images/garant/letter.png'

export const Garantier = () => {

    return (
      <section className={styles.wrapper}>
        <div className={styles.content}>
          <div className={styles.card}>
            <span className={cn(styles.isPay, styles.white)}></span>
            <div className={styles.textContent}>
              <h3 className={styles.title}>
                 <span className={styles.yellow}>20 000 </span>  {" "}
                <span className={styles.bold}>14 </span>
              </h3>

              <div className={styles.list}>
                <div className={styles.circle} />
                <div className={styles.bigText}>
                       {" "}
                  <span className={styles.bold}></span> 
                  
                </div>
              </div>
              <p className={styles.smallText}>
                    ,     
                   .  , 
                        
                .
              </p>
            </div>
            <img src={letter} alt={"letter"} className={styles.letter} />
          </div>

          <div className={styles.card}>
            <span className={cn(styles.isPay, styles.yellow)}></span>
            <div className={styles.textContent}>
              <h3 className={styles.title}>
                  <br></br>
                <span className={styles.yellow}> 100 000  </span>
                 <span className={styles.bold}>60 </span>
              </h3>

              <div className={styles.list}>
                <div className={styles.circle} />
                <div className={styles.bigText}>
                    {/* <span className={styles.fatText}></span> */}
                        {" "}
                  <span className={styles.bold}>100 000 </span>
                </div>
              </div>
              <p className={styles.smallText}>
                   60     
                    ,   
                   .
              </p>
            </div>
            <img src={letter} alt={"letter"} className={styles.letter} />
          </div>
        </div>
      </section>
    );
};

